body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
* {
  box-sizing: border-box; }

html,
body,
#root {
  width: 100%;
  height: 100%; }

.app-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(/WeatherApp/static/media/duotone.40c71e92.png);
  background-size: cover;
  overflow: hidden; }
  .app-container .main-container {
    width: 80%;
    height: 80%;
    display: flex;
    flex-direction: column;
    border: 1px solid #999;
    border-radius: 3px;
    -webkit-transform: translateY(-5%);
            transform: translateY(-5%); }
    @media screen and (max-width: 768px) {
      .app-container .main-container {
        width: 100%;
        height: 100%;
        -webkit-transform: translateY(0%);
                transform: translateY(0%); } }
    .app-container .main-container .top-section {
      flex: 0 0 50%; }
    .app-container .main-container .loading {
      color: #fff;
      background-color: #445a71;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; }
    .app-container .main-container .bottom-section {
      flex: 0 0 50%; }

.btn {
  border: 1px solid #ddd;
  margin: 15px 0;
  cursor: pointer; }
  .btn:focus {
    outline: none; }

.popup-container {
  position: absolute;
  will-change: transform;
  top: -60px;
  left: -29px;
  -webkit-transform: translate3d(487px, 177px, 0px);
          transform: translate3d(487px, 177px, 0px);
  width: 14em;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  text-align: center;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0px 0px 5px 2px rgba(15, 15, 15, 0.2);
  font-size: 16px; }

.form-container {
  padding: 5px;
  width: 100%; }
  .form-container label {
    font-size: 1.2rem; }
  .form-container input {
    border-radius: 3px;
    height: 3em;
    width: 90%;
    border: none;
    background-color: #eee; }

@-webkit-keyframes donut-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes donut-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.donut {
  display: inline-block;
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-left-color: #7983ff;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  -webkit-animation: donut-spin 1.2s linear infinite;
          animation: donut-spin 1.2s linear infinite; }

.top-container {
  display: flex;
  flex-direction: column;
  font-family: Oxygen, sans-serif;
  align-items: center;
  background-color: #445a71;
  height: 100%;
  width: 100%;
  padding: 1em;
  justify-content: space-between; }
  .top-container .title {
    color: #fff;
    font-size: 2em;
    font-weight: bold; }

.weather-container {
  text-align: center;
  margin-top: 1.2em;
  color: #fff; }
  .weather-container .header {
    font-size: 1.2em; }
  .weather-container .inner-container {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 1.5em;
    margin: 10px 0; }
    .weather-container .inner-container .current-weather {
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      font-size: 28px; }
    .weather-container .inner-container .image {
      width: 4em;
      height: 4em; }
      .weather-container .inner-container .image img {
        width: 100%;
        height: 100%; }
  .weather-container .footer {
    font-size: 1.2em; }

.btn-select-location {
  border: none;
  background-color: #49dcc2;
  font-size: 1em;
  font-weight: bold;
  font-family: Roboto, sans-serif;
  border-radius: 2px;
  color: #333;
  padding: .6em .8em;
  margin-top: .5em;
  -webkit-transition: all .5s ease;
  transition: all .5s ease; }
  .btn-select-location:hover, .btn-select-location:focus {
    cursor: pointer;
    background-color: #26c5a9; }

.bottom-container {
  width: 100%;
  height: 100%;
  background: -webkit-linear-gradient(top, #6abeca 0%, rgba(58, 98, 104, 0.6) 100%), url(/WeatherApp/static/media/sunny-weather.18533a59.jpeg) center no-repeat;
  background: linear-gradient(180deg, #6abeca 0%, rgba(58, 98, 104, 0.6) 100%), url(/WeatherApp/static/media/sunny-weather.18533a59.jpeg) center no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center; }
  .bottom-container .inner-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: baseline; }

.forcastday-container {
  width: 9em;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }
  .forcastday-container .image {
    width: 3em;
    height: 3em; }
    .forcastday-container .image img {
      width: 100%;
      height: 100%; }
  .forcastday-container .text {
    font-size: 1.2em;
    color: #fff; }
  .forcastday-container .muted-text {
    text-align: center;
    font-size: 1em;
    color: #000;
    font-weight: bold;
    word-wrap: wrap; }

